<template>
  <div>
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <header>
      <navbar></navbar>
    </header>
    <div class="main mt-5">
      <div class="container">
        <div class="row align-items-center pt-3 pt-lg-5">
          <div class="col-12 col-lg-8">
            <banner></banner>
          </div>
          <div class="col-12 col-lg-4">
            <form class="form-signin text-dark-blue" @submit.prevent="signin">
              <h1 class="h3 mb-3 font-weight-normal text-center">
                
                <i class="fas fa-user"></i>
              </h1>
              <input
                v-validate="'required|email'"
                name="email"
                type="email"
                id="inputEmail"
                class="form-control mb-3"
                placeholder="Email"
                autofocus
                v-model="user.username"
                autocomplete="on"
              />
              <span class="text-danger">{{ errors.first('email') }}</span>
              <input
                v-validate="'required|min:6'"
                name="password"
                type="password"
                id="inputPassword"
                class="form-control mb-1"
                placeholder=""
                v-model="user.password"
                autocomplete="on"
              />
              <span class="text-danger">{{ errors.first('password') }}</span>
              <div class="checkbox mb-3">
                <label>
                  <input type="checkbox" value="remember-me" /> 
                </label>
              </div>
              <div class="text-right">
                <button class="btn btn-md btn-submit btn-inline-block mr-2"></button>
                <button class="btn btn-md btn-success btn-inline-block" type="submit"></button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <footer class="mt-4">
      <footer-info></footer-info>
    </footer>
  </div>
</template>

<script>
import navbar from '@/components/FrontEnd/NavComponents/Navbar'
import banner from '@/components/FrontEnd/CategoryComponents/Banner'
import footerInfo from '@/components/FrontEnd/FooterComponents/FooterInfo'
export default {
  name: 'login',
  data () {
    return {
      user: { username: '', password: '' },
      isLoading: false
    }
  },
  components: {
    navbar,
    banner,
    footerInfo
  },
  methods: {
    signin: function () {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.APIPATH}/admin/signin`
      this.$http
        .post(api, {
          username: vm.user.username,
          password: vm.user.password
        })
        .then(response => {
          if (response.data.message === '') {
            vm.isLoading = false
            vm.$router.push('/admin/products')
          } else {
            vm.isLoading = false
            vm.$bus.$emit('message:loginFail', '')
          }
        })
    }
  }
}
</script>

<style scoped>
.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



