  <template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(product,index) in orders" :key="product.id">
          <td>{{translateTime[index]}}</td>
          <td>{{product.user.email}}</td>
          <td>
            <tr v-for="(order,index) in product.products" :key="index">
              <td class="border-0">{{order.product.title}}</td>
              <td>{{order.qty}}</td>
            </tr>
          </td>
          <td class="text-right">{{product.total |currencyFilter}}</td>
          <td>
            <span v-if="product.is_paid"></span>
            <span v-else></span>
          </td>
          <td v-if="product">
            <button class="btn btn-outline-primary btn-sm" @click="openModel(product)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :page-item="pegeItem"></pagination>

    <!-- Modal -->
    <div
      class="modal"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="name"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="name"
                    placeholder=""
                    v-model="newProduct.user.name"
                  />
                </div>

                <div class="form-group">
                  <label for="address"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="address"
                    placeholder=""
                    v-model="newProduct.user.address"
                  />
                </div>
                <div class="form-row">
                  <div class="form-group col-4">
                    <label for="tel"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="tel"
                      placeholder=""
                      v-model="newProduct.user.tel"
                    />
                  </div>
                  <div class="form-group col-8">
                    <label for="email"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="email"
                      placeholder="Email"
                      v-model="newProduct.user.email"
                    />
                  </div>
                </div>
                <div>
                  <p> {{newProduct.total | currencyFilter}}</p>

                  <p>
                    
                    <template>
                      <span
                        v-if="newProduct.is_paid"
                        :class="{'text-success':newProduct.is_paid}"
                      ></span>
                      <span v-else :class="{'text-danger':newProduct.is_paid==false}"></span>
                    </template>
                  </p>
                </div>
                <hr />
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                    v-model="newProduct.message"
                  ></textarea>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="confirmProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import pagination from '../Dashboard/Pagination'

export default {
  data: function () {
    return {
      orders: [],
      newProduct: { user: { name: '', tel: '', address: '' } },
      isLoading: false,
      pegeItem: {}
    }
  },
  components: {
    pagination
  },

  methods: {
    getOrders: function () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders`;
      vm.isLoading = !vm.isLoading
      this.$http.get(api).then(response => {
        console.log(response.data)
        this.pegeItem = response.data.pagination
        this.orders = response.data.orders
        vm.isLoading = !vm.isLoading
      })
    },
    openModel (product) {
      $('#productModal').modal('show')
      if (product.id) {
        this.newProduct = product
      }
    },
    modifyModel (product) {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${product.id}`
      vm.$http.put(api, vm.newProduct).then(response => {
        if (response.data.success) {
          console.log(response.data.message)
          vm.getOrders()
        }
      })
    },
    isEnable () {
      if (this.newProduct.is_enabled === true) {
        this.newProduct.is_enabled = 1
      } else {
        this.newProduct.is_enabled = 0
      }
    },
    confirmProduct () {
      const vm = this
      if (vm.newProduct.id) {
        // 
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.newProduct.id}`;
        vm.$http.put(api, { data: vm.newProduct }).then(response => {
          if (response.data.success) {
            vm.getProducts()
          }
        })
      } else {
        // 
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
        vm.$http.post(api, { data: vm.newProduct }).then(response => {
          if (response.data.success) {
            vm.isLoading = !vm.isLoading
            console.log(response.data.message)
            vm.getProducts()
            vm.isLoading = !vm.isLoading
          }
        })
      }
      $('#productModal').modal('hide')
    }
  },
  computed: {
    translateTime () {
      const timeStamp = []
      this.orders.forEach(time => {
        const date = new Date(time.create_at * 1000)
        const Y = date.getFullYear() + '-'
        const M =
          (date.getMonth() + 1 < 10
            ? '0' + (date.getMonth() + 1)
            : date.getMonth() + 1) + '-'
        const D = date.getDate() + ' '
        const h = date.getHours() + ':'
        const m = date.getMinutes() + ':'
        const s = date.getSeconds()
        timeStamp.push(Y + M + D + h + m + s)
      })

      return timeStamp
    }
  },
  created () {
    const vm = this
    vm.getOrders()
  }
}
</script>



