<template>
  <!-- Vue native DOM use native  -->
  <a
    href="#"
    class="shopping-cart text-dark-blue text-decoration-none d-block"
    @click.prevent="addToCart"
  >
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <slot></slot>
    <i class="fas fa-shopping-cart"></i>
  </a>
</template>

<style lang="scss" scoped>
.shopping-cart:hover {
  color: #ff6f61;
}
</style>

<script>
export default {
  data () {
    return {
      isLoading: false
    }
  },
  props: {
    productId: { type: String }, // ID
    number: { type: Number, default: 1 }// 
  },
  methods: {
    addToCart () {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
      if (vm.number !== null) {
        vm.$http
          .post(api, { data: { product_id: vm.productId, qty: vm.number } })
          .then(res => {
            if (res.data.message === '') {
              vm.$bus.$emit('message:addTocart', '') // 
              vm.$bus.$emit('getTotalOrders') // 
              vm.isLoading = false
            }
          })
      } else {
        vm.$bus.$emit('message:selectNumber', '') // 
        vm.isLoading = false
      }
    }
  }
}
</script>



